<template>
  <table class="table table-hover table-sm">
    <thead>
      <tr>
        <th class="col-2">이름</th>
        <th class="col-1">착용 레벨</th>
        <th class="col-2">대상 아이템</th>
        <th class="col-4">룬 순서</th>
        <th class="col-3">option</th>
      </tr>
    </thead>
    <tbody>
      <tr
        v-for="runeword in runewordList"
        :key="runeword.nameKo"
        :class="isRunewordDisplay(runeword) ? '' : 'd-none'"
      >
        <td>{{ runeword.nameKo }} ({{ runeword.nameEn }})</td>
        <td v-text="runeword.reqLv > 0 ? runeword.reqLv : '-'"></td>
        <td>
          <ul class="list-group list-group-flush">
            <li
              class="list-group-item"
              v-for="itemCategory in runeword.itemCategoryList"
              :key="itemCategory"
            >
              ({{ runeword.runeList.length }})
              {{ getItemCategory(itemCategory).categoryKo }}
            </li>
          </ul>
        </td>
        <td>
          <RuneButton
            v-for="rune in runeword.runeList"
            :key="rune"
            :id="getRune(rune).id"
          />
        </td>
        <td
          v-html="runeword.completeStats.replace(/(?:\r\n|\r|\n)/g, '<br />')"
        ></td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import runewordList from "@/assets/diablo2/data/runeword.json";
import runeList from "@/assets/diablo2/data/rune.json";
import itemCategoryList from "@/assets/diablo2/data/itemCategory.json";

import RuneButton from "@/components/diablo2/RuneButton.vue";

export default {
  components: { RuneButton },
  props: {
    selectedRuneIdList: Array,
    selectedItemCategoryList: Array,
    searchKeyword: String,
  },
  data() {
    return {
      runewordList: runewordList,
      runeList: runeList,
      itemCategoryList: itemCategoryList,
    };
  },
  methods: {
    getRune(id) {
      var targetRune = null;
      for (var i = 0; i < runeList.length; i++) {
        if (runeList[i].id == id) {
          targetRune = runeList[i];
          break;
        }
      }
      return targetRune;
    },
    getItemCategory(category) {
      var targetItemCategory = null;
      for (var i = 0; i < itemCategoryList.length; i++) {
        if (itemCategoryList[i].category == category) {
          targetItemCategory = itemCategoryList[i];
          break;
        }
      }
      return targetItemCategory;
    },
    isRunewordDisplay(runeword) {
      // 여기서 보여줄지 말지 결정, 여러 조건에 부합하는 경우를 결정해야함.
      // 1. 선택한 룬
      // 2. 선택한 아이템
      // 3. 검색어
      var isDisplay = true;

      //선택한 룬이 해당되는 룬워드 검색. and 조건이어야함
      if (
        this.selectedRuneIdList !== undefined &&
        this.selectedRuneIdList.length > 0
      ) {
        isDisplay = true;
        for (var i = 0; i < this.selectedRuneIdList.length; i++) {
          var isTargetRuneInclude = false;
          for (var j = 0; j < runeword.runeList.length; j++) {
            if (this.selectedRuneIdList[i] == runeword.runeList[j]) {
              isTargetRuneInclude = true;
            }
          }
          if (!isTargetRuneInclude) {
            isDisplay = false;
          }
        }
      }

      // selectedRuneIdList 대상이 아닌 경우 비노출 처리
      if (!isDisplay) {
        return isDisplay;
      }
      // 검색어에 해당하는 runeword인지 확인
      if (this.searchKeyword !== undefined && this.searchKeyword !== "") {
        if (
          runeword.nameKo.indexOf(this.searchKeyword) >= 0 ||
          runeword.nameEn.indexOf(this.searchKeyword) >= 0 ||
          runeword.completeStats.indexOf(this.searchKeyword) >= 0
        ) {
          isDisplay = true;
        } else {
          isDisplay = false;
        }
      }

      return isDisplay;
    },
  },
};
</script>

<style></style>
